import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions/actions'
import * as mutations from './mutations/mutations'
import * as getters from './getters/getters'
import * as gettersDocentes from './getters/docentes'
import * as gettersNoDocentes from './getters/noDocentes'
import * as gettersInvestigadores from './getters/investigadores'
import state from './states/state'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    ...state
  },
  mutations: {
    ...mutations
  },
  actions: {
    ...actions
  },
  getters: {
    ...getters,
    ...gettersDocentes,
    ...gettersNoDocentes,
    ...gettersInvestigadores
  }
})
