import * as html2canvas from 'html2canvas'
const plantaDocente = () => import(/* webpackChunkName: "data" */ '@/../data/plantaDocente.json')

// Configuración inicial por default.
export const init = async (context) => {
  const data = await plantaDocente().then(({ default: myData }) => myData)
  context.commit('SET_LISTA_INDICADORES', [{ anio: '2022', periodo: '1' }])
  context.commit('SET_INDICADORES', [{ _id: '2022-1', data }])
  context.dispatch('selectCurrentPeriodo', { anio: '2022', periodo: '1' })
  context.dispatch('getDarkMode')
  context.state.loaded = true
}

// Selecciona el periodo que se está analizando. (primer o segundo semestre).
export const selectCurrentPeriodo = (context, payload) => {
  context.commit('SELECT_PERIODO', payload)
  const curData = context.state.indicadoresData.find(i => i._id === `${payload.anio}-${payload.periodo}`)
  context.commit('SET_PERIODO', curData.data)
}

export const loading = (context, payload) => {
  context.commit('SET_LOADING', payload)
}

export const getDarkMode = context => {
  context.commit('GET_DARKMODE')
}

export const setDarkMode = context => {
  context.commit('SET_DARKMODE')
}

// Acciones para descargar los indicadores en .png
export const createPng = (context, payload) => {
  context.commit('SET_LOADING_OVERLAY', true)
  // context.state.loadingOverlay = true
  const card = document.getElementById(payload)
  // Agrega el footer al final
  const textContainer = document.createElement('div')
  textContainer.id = 'textContainer'
  textContainer.style.width = '100%'
  textContainer.style.backgroundColor = context.state.dark ? '#2C2C2E' : 'white'
  textContainer.style.paddingLeft = '10px'
  textContainer.style.paddingRight = '10px'
  textContainer.style.fontSize = '12px'
  const text = document.createTextNode('Fuente: UNLA.')
  textContainer.appendChild(text)
  card.appendChild(textContainer)
  html2canvas(
    card
    ,
    {
      logging: true,
      letterRendering: false,
      allowTaint: false,
      scale: context.getters.mobileSize ? 600 / window.innerWidth : 1,
      backgroundColor: context.state.dark ? '#2C2C2E' : 'white',
      scrollX: 0,
      scrollY: -window.scrollY,
      ignoreElements: (element) => element.id === 'downloadBtn'
    })
    .then(canvas => {
      context.dispatch('saveAs', { file: canvas.toDataURL('image/png'), fileName: `UNLA-indicadores-${payload}.png` })
    })
    .catch(err => {
      console.log(err)
      context.commit('SET_LOADING_OVERLAY', false)
    })
    .finally(x => {
      textContainer.remove()
    })
}

export const saveAs = (context, payload) => {
  const link = document.createElement('a')
  // const footer = document.getElementById('textContainer')
  if (typeof link.download === 'string') {
    link.href = payload.file
    link.download = payload.fileName

    // Firefox requires the link to be in the body
    document.body.appendChild(link)

    // simulate click
    link.click()

    // remove the link when done
    document.body.removeChild(link)
    context.commit('SET_LOADING_OVERLAY', false)
  } else {
    window.open(payload.file)
    context.commit('SET_LOADING_OVERLAY', false)
  }
}
