export const SET_LOADING_OVERLAY = (state, payload) => {
  state.loadingOverlay = payload
}

export const SET_LISTA_INDICADORES = (state, payload) => {
  state.listaIndicadores = payload
}

export const SELECT_PERIODO = (state, payload) => {
  state.periodoSeleccionado = payload
}

export const SET_INDICADORES = (state, payload) => {
  state.indicadoresData = payload
}

export const SET_PERIODO = (state, payload) => {
  state.periodoSeleccionadoData = payload
}

export const SET_LOADING = (state, payload) => {
  state.loading = payload
}

export const GET_DARKMODE = state => {
  state.dark = window.localStorage.getItem('unla-indicadores-dark') === 'true'
  window.vm.$vuetify.theme.dark = state.dark
}

export const SET_DARKMODE = state => {
  state.dark = !state.dark
  window.vm.$vuetify.theme.dark = state.dark
  window.localStorage.setItem('unla-indicadores-dark', state.dark)
}
