export const docentes = (state, getters) => {
  return state.periodoSeleccionadoData
    .filter(d =>
      d.descripcion_grupo === 'Docente Ordinario' ||
      d.descripcion_grupo === 'Doc Ordinario CCT' ||
      d.descripcion_grupo === 'Doc Interino CCT' ||
      d.descripcion_grupo === 'Docente Interino')
    .filter(d =>
      d.unidad_academica === 'ICI' ||
      d.unidad_academica === 'IDH' ||
      d.unidad_academica === 'IDEI' ||
      d.unidad_academica === 'ICO'
    )
}

export const mafs = (state, getters) => {
  return state.periodoSeleccionadoData
    .filter(m => m.descripcion_grupo === 'C MAF PS')
}

export const preuniversitarios = (state, getters) => {
  return state.periodoSeleccionadoData
    .filter(d =>
      d.descripcion_grupo === 'Doc Preuniv nd Ordin' ||
      d.descripcion_grupo === 'Doc Preuniv Ordinari' ||
      d.descripcion_grupo === 'Doc Int Preuniversit' ||
      d.descripcion_grupo === 'Doc Preuniv nd'
    )
}
