import Vue from 'vue'
import VueRouter from 'vue-router'
const PortadaPrincipal = () => import(/* webpackChunkName: "PortadaPrincipal" */ '../views/PortadaPrincipal.vue')
const Docentes = () => import(/* webpackChunkName: "Docentes" */ '../views/gestion/Docentes.vue')
const NoDocentes = () => import(/* webpackChunkName: "NoDocentes" */ '../views/gestion/NoDocentes.vue')
const Investigadores = () => import(/* webpackChunkName: "Investigadores" */ '../views/investigacion/Investigadores.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'PortadaPrincipal',
    component: PortadaPrincipal
  },
  {
    path: '/docentes',
    name: 'Docentes',
    component: Docentes
  },
  {
    path: '/nodocentes',
    name: 'NoDocentes',
    component: NoDocentes
  },
  {
    path: '/investigadores',
    name: 'Investigadores',
    component: Investigadores
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: '/',
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes
})

export default router
