export const nodocentes = (state, getters) => {
  return state.periodoSeleccionadoData
    .filter(d =>
      d.descripcion_grupo === 'No Doc. Permanente' ||
      d.descripcion_grupo === 'No Docente Interino' ||
      d.descripcion_grupo === 'No Doc. Contratado'
    )
}

export const nodocentesCargos = (state, getters) => {
  return getters.nodocentes
    .filter(nodocente => {
      const cate1 = nodocente.descripcion_categoria === 'Cat 1 CCT 366/06'
      const cate2 = nodocente.descripcion_categoria === 'Cat 2 CCT 366/06'
      const cate3 = nodocente.descripcion_categoria === 'Cat 3 CCT 366/06'
      const cate4 = nodocente.descripcion_categoria === 'Cat 4 CCT 366/06'
      const cate5 = nodocente.descripcion_categoria === 'Cat 5 CCT 366/06'
      const cate6 = nodocente.descripcion_categoria === 'Cat 6 CCT 366/06'
      const cate7 = nodocente.descripcion_categoria === 'Cat 7 CCT 366/06'
      return cate1 || cate2 || cate3 || cate4 || cate5 || cate6 || cate7
    })
}

export const nodocentesPrestaciones = (state, getters) => {
  return getters.nodocentes
    .filter(nodocente => {
      const pselyt = nodocente.descripcion_categoria === 'Prest. Serv. Sec LyT'
      const psemem = nodocente.descripcion_categoria === 'Prest Serv Com Mem'
      const psegen = nodocente.descripcion_categoria === 'Prest. Serv. Genero'
      const psedep = nodocente.descripcion_categoria === 'Prest Serv Dep' || nodocente.descripcion_categoria === 'Prest. Serv. Deporte' || nodocente.descripcion_categoria === 'Prest. Serv. Dep'
      const psemul = nodocente.descripcion_categoria === 'Prest. Serv. Multi'
      const psemed = nodocente.descripcion_categoria === 'Prest de Serv Medios'
      const psecul = nodocente.descripcion_categoria === 'Prest. Serv. Cult'
      const contasju = nodocente.descripcion_categoria === 'Contr.Pres.Serv ASJU'
      const contesp = nodocente.descripcion_categoria === 'Cont. Proy. Esp.'
      const conmed = nodocente.descripcion_categoria === 'Contr Prest Serv Med'
      const chofer = nodocente.descripcion_categoria === 'CHOFER'
      return pselyt || psemem || psegen || psedep || psemul || psemed || psecul || contasju || contesp || conmed || chofer
    })
}

